<template>
  <div>
    <Layout class='layout-container'>
      <Header class="layout-head">
        <Menu mode="horizontal" theme="dark" style="border-bottom:0px;background-color: #1a2942;height:60px" ref='headermenu'>
          <div class="layout-logo"></div>
          <div class="layout-nav header-nav">
            <MenuItem v-for='(menu,i) in menus' :key='menu.name' :class='{active:menu.name==activeName}' :name='menu.name' @click.native='routeTo(menu.path)'>
            <Icon :type='menu.icon' v-if='menu.icon'></Icon>
            {{menu.title}}
            </MenuItem>
          </div>
          <div style='float:right;margin-right:30px;'>
            <Dropdown @on-click='loginOut'>
              <Avatar style="background-color: #3bc0c3;cursor:pointer" icon="person" @click.native='loginOut' />
              <DropdownMenu slot="list">
                <!-- <DropdownItem>个人信息</DropdownItem> -->
                <DropdownItem name='loginOut'>退出登录</DropdownItem>
              </DropdownMenu>
            </Dropdown>

          </div>
        </Menu>
      </Header>
      <Layout>
        <router-view></router-view>
      </Layout>
    </Layout>
  </div>
</template>
<script>
import { removeToken } from './utils.js'
export default {
  data() {
    return {
      activeName: '0',
      menus: [{
        name: '1',
        icon: 'ios-monitor',
        title: '首页',
        path: '/home'
      }, 
      {
        name: '2',
        icon: 'ios-navigate',
        title: '更新日志',
        path: '/logs'
      }, {
        name: '3',
        icon: 'ios-analytics',
        title: '购买续费',
        path: '/renew'
      }, {
        name: '4',
        icon: 'ios-paper',
        title: '关于',
        path: '/about'
      }
      ] 
    }
  },
  watch: {
    $route(to, from) {
        let filter = this.menus.find(it => it.path == to.path)
        this.activeName = filter ? filter.name : '1'
    }
  },
  methods: {
    routeTo(path) {
      if (path=='/home') {
        this.$router.push(path);
      } else {
        this.$Message.success('模块正在开发中，敬请期待')
      }
    },
    loginOut(name) {
      if(name!=='loginOut'){
        return;
      }
      this.$Modal.confirm({
        title: '退出登录',
        content: '确认退出登录吗？',
        okText: '确认',
        cancelText: '取消',
        onOk: () => {
          removeToken()
          this.$router.push('/login')
        },

      })
    },
  },
  mounted() {
    let path = this.$route.path
    let menus = this.menus
    let filter = menus.find(it => it.path == path)
    if (filter) {
      this.activeName = filter.name
    } else {
      this.activeName = '1'
    }
  }
}

</script>
<style lang="less">
body {
  min-width: 1080px;
}
.ivu-input-group-append .ivu-btn-ghost:hover {
  color: #62cdcf;
  background-color: transparent;
  border-color: #62cdcf;
}

.layout-container {
  width: 100vw;
  height: 100vh;
}

.ivu-menu-dark.ivu-menu-horizontal .header-nav .ivu-menu-item.active {
  color: #fff;
  background-color: #3bc0c3;
}

.ivu-menu-dark.ivu-menu-horizontal .header-nav .ivu-menu-item {
  color: #3bc0c3;
}

.layout {
  background: #f1f4f5;
  border-radius: 4px;
  width: 100%;
}

.layout-logo {
  width: 100px;
  height: 30px; // background-color:#48c4c7;
  border-radius: 3px;
  float: left;
  position: relative;
  top: 15px;
  left: 20px;
}

.layout-head {
  background-color: #1a2942;
  width: 100%;
  height: 60px;
}

.layout-nav {
  width: 600px;
  margin: 0 auto;
  margin-right: 10px;
  color: #48a4bd;
}

.layout-footer-center {
  position: fixed;
  height: 30px;
  text-align: center;
  color: #ccc;
  background-color: #fff;
  bottom: 30px;
}

.layout-content {}

.menu-color {
  color: #48a4bd;
}

.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
  background-color: #fff;
}

.ivu-menu-submenu.ivu-menu-opened {
  background-color: #f6f5f5;
}

.ivu-breadcrumb.ivu-breadcrumb-item-separator {
  color: #ccc;
}

.ivu-checkbox-inner {
  border-color: #ddd;
}

.vue-html5-editor>.toolbar {
  z-index: 1000 !important;
}
.ivu-dropdown-item:hover {
  background-color: #3bc0c3;
}
.clear-fix::after {
  content: '';
  display: block;
  clear: both;
}
</style>



